<template>
    <div class="pos">
        <div>
            <el-row>
                <el-col :span="7" class="post-order" id="order-list">
                    <el-tabs :stretch=true>
                        <el-tab-pane label="点餐">
                            <el-table :data='tableData' border style='width:100%'>
                                <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                                <el-table-column prop="count" label="数量"></el-table-column>
                                <el-table-column prop="price" label="价格"></el-table-column>
                                <el-table-column label="操作" fixed='right'>
                                    <template scope="scope">
                                        <el-button type="text" size="small" @click="delSingleGood(scope.row)">删除</el-button>
                                        <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                            <div class="totalList">
                                <small>总数：</small>{{totalCount}}   <small> 总价格：</small> {{totalPrice}}
                            </div>
                            <div class="div-btn">
                                <el-button type="warning">挂单</el-button>
                                <el-button type="danger" @click="delAllGoods">删除</el-button>
                                <el-button type="success" @click="checkOut">结账</el-button>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="挂单">

                        </el-tab-pane>
                        <el-tab-pane label="外卖">

                        </el-tab-pane>
                    </el-tabs>
                </el-col>
                <el-col :span="17">
                    <div class="often-goods">
                        <div class="title">热门商品</div>
                        <div class="often-goods-list">
                            <ul>
                                <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                                    <span>{{goods.goodsName}}</span>
                                    <span class="o-price">￥{{goods.price}}元</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="good-type">
                        <el-tabs :stretch=true>
                            <el-tab-pane label="汉堡">
                                <ul class="cookList">
                                    <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="小食">
                                <ul class="cookList">
                                    <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="饮料">
                                <ul class="cookList">
                                    <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="套餐">
                                <ul class="cookList">
                                    <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                        </el-tabs>
                        
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>  
</template>


<script>
import axios from "axios";
export default {
  name: "pos",
  data() {
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalPrice: 0,
      totalCount: 0
    };
  },
  created: function() {
    axios
      .get(
        "https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods"
      )
      .then(response => {
        console.log(response);
        this.oftenGoods = response.data;
      })
      .catch(error => {
        alert("数据请求错误，请刷新页面");
      });
    axios
      .get(
        "https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods"
      )
      .then(response => {
        console.log(response);
        this.type0Goods = response.data[0];
        this.type1Goods = response.data[1];
        this.type2Goods = response.data[2];
        this.type3Goods = response.data[3];
      })
      .catch(error => {
        alert("数据请求错误，请数刷新页面");
      });
  },
  mounted: function() {
    var orderHeight = document.body.clientHeight;
    document.getElementById("order-list").style.height = orderHeight + "px";
  },
  methods: {
    addOrderList: function(goods) {
      this.totalPrice = 0;
      this.totalCount = 0;
      let isHave = false;
      //判断选中的商品是否已经存在
      for (let i = 0; i < this.tableData.length; i++) {
        if (goods.goodsId === this.tableData[i].goodsId) {
          isHave = true;
          //选中的商品已经存在
          this.tableData[i].count++;
        }
      }
      if (!isHave) {
        this.tableData.push({
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          count: 1,
          price: goods.price
        });
      }
      //   计算总数量和总钱数
      this.getAll();
    },
    delSingleGood: function(goods) {
      this.tableData = this.tableData.filter(e => {
        return e.goodsId != goods.goodsId;
      });
      this.getAll();
    },
    delAllGoods: function() {
      this.totalCount = 0;
      this.totalPrice = 0;
      this.tableData = [];
    },
    getAll: function() {
      //   计算总数量和总钱数
      this.totalCount = 0;
      this.totalPrice = 0;
      this.tableData.forEach(ele => {
        this.totalPrice = this.totalPrice + ele.count * ele.price;
        this.totalCount += ele.count;
      });
    },
    checkOut: function() {
      if(this.totalCount != 0) {
        this.delAllGoods();
        this.$message({
          message: '结账成功，感谢您的付出！',
          type: 'success'
        })
      }else{
        this.$message.error('不能空结，加油！')
      }
    }
  }
};
</script>

<style>
* {
  text-align: center;
}
#order-list {
  background-color: #f9fafc;
  border-right: 1px solid #c0ccda;
}
.div-btn {
  margin-top: 10px;
  text-align: center;
}
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
}
.often-goods-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 5px;
  background-color: #fff;
  cursor: pointer;
}
.o-price {
  color: #58b7ff;
}
.good-type {
  clear: both;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auto;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cookList li span {
  display: block;
  float: left;
}
.foodImg {
  width: 40%;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.totalList {
  width: 100%;
  height: 20px;
  background-color: #f9fafc;
  margin-top: 10px;
}
</style>


